@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Montserrat+Alternates&family=Silkscreen:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

body {
  background-image: url("website-background.jpg");
}

.navbar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: black;
  opacity: 0.8;
  font-size: 18px;
}

.nav-content-left {
  display: flex;
  width: 175px;
  justify-content: space-around;
  padding: 10px;
  align-items: center;
}

.nav-content-left:hover {
  transition: 0.4s;
  cursor: pointer;
  color: #FFFDE3 !important;
}

.nav-heading {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Silkscreen', cursive !important;
  font-size: 25px;
  text-decoration: none;
}

.nav-heading:hover {
  transition: 0.4s;
  cursor: pointer;
  color: #FFFDE3;
}

.circle {
  color: red;
}

.nav-link {
  text-decoration: none;
}

.nav-link:hover {
  transition: 0.4s;
  color: black;
}

.content-tab {
  margin-top: 3%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.inner-carousel {
  display: flex;

}

.carousel {
  width: 30%;
  cursor: grab;
  overflow: hidden;
}

.tabs {
  font-family: 'Montserrat', sans-serif !important;
  border: 1px solid black;
  border-radius: 16px;
  padding: 10px 20px;
  text-align: center;
  background-color: #201e1e;
  margin: 5px;
  font-size: 14px;
}

.tabs-clicked {
  transition: 0.3s;
  color: white;
  background-color: black;

  font-family: 'Montserrat', sans-serif !important;
  border: 1px solid black;
  border-radius: 16px;
  padding: 10px 20px;
  text-align: center;
  margin: 5px;
  font-size: 14px;

}

.news-item {
  box-shadow: 0 0 3px rgba(0,0,0,0.1);
}

.news-date {
  font-size: 13px;
  color: #CFD2CF;
  padding-bottom: 10px;
  padding-top: 10px;
}

.news-heading {
  padding-bottom: 10px;
  padding-top: 10px;
}

.news-content {
  padding-bottom: 10px;
  padding-top: 10px;
  line-height: 1.5;
}

.news-image {
  width: 100%;
  display: block;
}

.news-image:hover {
  transition: 0.4s;
  opacity: 0.8;
}
.inner-carousel a {
  text-decoration: none;
}
.top-content{
  margin-top: 3%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px;
  overflow-y: hidden;
}
.top-content h2 {
  margin-bottom: 0.5%;
}
.content-grid{
  max-width: 2000px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px,1fr));
  gap: 20px;
}
.content-grid:nth-child(1){
  grid-column-start: 1;
  grid-column-end: 3;
}
.spinner{
  display: flex;
  justify-content: center;
  align-items: center;
}
.search{
 display: flex;
 align-items: center;
 background-color: white;
 padding: 10px;
 border-radius: 16px;
 width: 100%;
 
}
.search-box{
  margin-left: 5%;
  outline: none;
  border: none;
  color: black;
  font-size: 16px;
  overflow-x: hidden;
}
.search-icon{
  color: black;
  font-size: 16px;
  background: none;
  border: none;
  outline: none;
}
.search-icon:hover{
  transition: 0.4s;
  transform: rotate(360deg);
  color: crimson;
}